import moment from 'moment'

export default class Todo {
  constructor (json = {}) {
    // this.id = json.id
    this.isShow = true
    this.name = json.name || ''
    this.dateLine = json.date || moment().format('YYYY-MM-DD')
    this.group = json.group || 'today'
    this.secondaryTodo = json.secondaryTodo || []
    this.localCity = json.localCity || ''
    this.localAddress = json.localAddress || ''
    this.note = json.note || ''
    this.isCheck = json.isCheck || false
    this.isImportant = json.isImportant || false
  }

  get groups () {
    return this.$store.state.groups
  }
}



